import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

/* eslint-disable no-param-reassign */
const store = new Vuex.Store({
  state: {
    isLoaded: false,
  },
  mutations: {
    loaded(state) {
      // mutate state
      state.isLoaded = true;
    },
  },
});
/* eslint-enable no-param-reassign */

export default store;



